<template>
    <div class ="nav">  
        <div class="container">
            <img src="../../../static/img/starbg.jpg"  alt="bgimage" class="bgImage"/>
            <div id="title">
                Welcome
            </div>
        </div>
    </div>
</template>
<script>
export default {
}
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
.nav {
    position: relative;
    .container {
        overflow: hidden;
        width: 100%;
    }
    .bgImage {
        display: block;
        width: 100%;
        transform: perspective(62.5rem) translateZ(0);
        transition:  8s;
        &:hover {
             transform: perspective(62.5rem) translateZ(6.25rem);
             transition:  8s;
         }
    }
    #title {
        font-family: Georgia, "Microsoft YaHei", "微软雅黑",  STXihei, "华文细黑",  serif;
        color: #fff;
        text-align: center;
        font-size: 3.125rem;
        width: 31.25rem;
        height: 3.125rem;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -0.625rem;
        margin-left: -15.625rem;
    }
}
@media screen and (max-width: 980px) {
    nav {
        width: 60% !important;
    }
}
@media screen and (max-width: 440px) {
    .container, .bgImage {
        height: 18rem !important;
    }
    #title {
        font-size: 2rem !important;
    }
    nav {
        width: 100% !important;
        ul {
            width: 100% !important;
            padding-left: 0 !important;
            li {
                font-size: 0.9rem !important;
                margin-right: 0 !important;
            }
        }
    }
}
</style>



