<template>
<div class="tab">
    <div class="tab-item"><button class="active" @click="goto(`#/index`)">首页</button></div>
    <div class="tab-item"><button class="active" @click="goto(`#/fileList`)">随笔</button></div>
    <div class="tab-item"><button class="active" @click="goto(`#/about`)">关于我</button></div>
<!--     <div class="tab-item"><router-link to ="/index" class="active" data-hover="Home">Home</router-link></div>
    <div class="tab-item"><router-link to ="/Blog" data-hover="Blog">Blog</router-link></div>
    <div class="tab-item"><router-link to ="/about" data-hover="About">About</router-link></div>
    <div class="tab-item"><router-link to ="/Contact" data-hover="Contact">Contact</router-link></div> -->
</div>
</template>
<script>
export default {
  methods: {
    goto (th) {
      global.location.href = th
    }
  }
}
</script>
<style lang="css">
.tab
{
    padding-top:5px;
    display:flex;
    width:100%;
    height:50px;
    line-height:25px;
    border-top :2px solid rgba(7,17,27,0.1);
    font-size:14px;
    text-align:center;
}
.tab-item
{
    margin-top:5px;
    flex:1;
}
/* .tab-item a {
  display: inline-block;
  padding: 1rem;
  color: #707070;
  text-decoration: none;
  transition: transform 0.3s ease 0s;
  transform-origin: 50% 0px 0px;
  transform-style: preserve-3d;
}
.tab-item a.active {
  color: #FFF;
  font-weight: 700;
}
.tab-item a:hover {
  background: #fff;
  color: #000;
  transform: rotateX(90deg) translateY(-22px);
}
.tab-item a::before {
  position: absolute;
  top: 100%;
  left: 0px;
  width: 100%;
  padding: 4px 0px;
  text-align: center;
  line-height: 50px;
  background: none repeat scroll 0% 0% #00bcd4;
  color: #FFF;
  content: attr(data-hover);
  transition: #00bcd4 0.3s ease 0s;
  transform: rotateX(-90deg);
  transform-origin: 50% 0px 0px;
} */
button{
  background:#000;
  color:#fff;
  border:none;
  position:relative;
  padding:0 2em;
  cursor:pointer;
  transition:800ms ease all;
  outline:none;
}
.tab-item button.active {
  color: #FFF;
}
button:hover{
  color:#03a9f4;
}
button:before,button:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: #03a9f4;
  transition:400ms ease all;
}
button:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
button:hover:before,button:hover:after{
  width:100%;
  transition:800ms ease all;
}
</style>



